'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Badge } from '@/components/ui/badge'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Label } from '@/components/ui/label'
import { Calculator } from 'lucide-react'

interface Ingredient {
  id: string
  name: string
  quantity: number
  unit: string
  notes?: string
}

interface IngredientsCalculatorProps {
  ingredients: Ingredient[]
  baseIngredient?: string // Par exemple "farine" pour la recette de base
}

export function IngredientsCalculator({ ingredients, baseIngredient }: IngredientsCalculatorProps) {
  const [multiplier, setMultiplier] = useState(1)
  const [baseQuantity, setBaseQuantity] = useState(0)
  const [totalWeight, setTotalWeight] = useState(0)

  // Trouver l'ingrédient de base (farine par défaut) et sa quantité
  useEffect(() => {
    const base = ingredients.find(ing => ing.name.toLowerCase().includes(baseIngredient?.toLowerCase() || 'farine'))
    if (base) {
      setBaseQuantity(base.quantity)
    }
  }, [ingredients, baseIngredient])

  // Calculer le poids total
  useEffect(() => {
    const total = ingredients
      .filter(ing => ing.unit === 'g' || ing.unit === 'kg')
      .reduce((sum, ing) => {
        const quantity = ing.unit === 'kg' ? ing.quantity * 1000 : ing.quantity
        return sum + (quantity * multiplier)
      }, 0)
    setTotalWeight(total)
  }, [ingredients, multiplier])

  // Convertir une quantité avec son unité
  const formatQuantity = (quantity: number, unit: string) => {
    if (unit === 'kg' && quantity * multiplier >= 1000) {
      return `${(quantity * multiplier / 1000).toFixed(2)} kg`
    }
    return `${(quantity * multiplier).toFixed(1)} ${unit}`
  }

  const handleBaseQuantityChange = (value: string) => {
    const newQuantity = parseFloat(value) || 0
    if (baseQuantity > 0) {
      setMultiplier(newQuantity / baseQuantity)
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Calculator className="h-5 w-5" />
          <span>Calculateur d'ingrédients</span>
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div className="flex items-end gap-4">
            <div className="flex-1 space-y-2">
              <Label>Quantité de {baseIngredient || 'farine'}</Label>
              <Input
                type="number"
                min="0"
                value={(baseQuantity * multiplier).toString()}
                onChange={(e) => handleBaseQuantityChange(e.target.value)}
              />
            </div>
            <Badge variant="secondary" className="mb-2">
              Masse totale: {totalWeight.toFixed(0)}g
            </Badge>
          </div>

          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Ingrédient</TableHead>
                <TableHead className="w-[100px] text-right">Quantité</TableHead>
                {/* <TableHead className="w-[150px]">Notes</TableHead> */}
              </TableRow>
            </TableHeader>
            <TableBody>
              {ingredients.map((ingredient) => (
                <TableRow key={ingredient.id}>
                  <TableCell>{ingredient.name}</TableCell>
                  <TableCell className="text-right font-mono">
                    {formatQuantity(ingredient.quantity, ingredient.unit)}
                  </TableCell>
                  {/* <TableCell>{ingredient.notes}</TableCell> */}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </CardContent>
    </Card>
  )
}
